import React from "react"
import Slider from "react-slick";
import Layout from "../../components/Layout"
import SEO from "../../components/SEO"
import FluidImage from "../../components/FluidImage"
import contentParser from "gatsby-wpgraphql-inline-images"

const Post = ({ pageContext }) => {
  const pluginOptions = {
    wordPressUrl: `http://gatsby.lamwebnhatrang.net/`,
    uploadsUrl: `http://gatsby.lamwebnhatrang.net/wp-content/uploads/`,
  }
  const {
    post: { title, content, CustomPost },
  } = pageContext
  var settings = {
    dots: false,
    infinite: true,
    autoplay: true,
    speed: 1000,
    slidesToShow: 1,
    fade: false,
    arrows: true,
    slidesToScroll: 1
  };
  console.log(CustomPost)
  return (
    <Layout>
      <SEO title={title} />
      <div className="big-title mb-5">
        <div className="container text-center">
          <h1>{title}</h1>
        </div>
      </div>
      <div className="container">
        <div className="row">
          <div className="col-md-9 col-xs-12">
            <div className="detail">
              {(() => {
                  if (CustomPost.address != null) {
                    return (
                      <>
                        <div className="overview pb-3">
                        <Slider {...settings} className="pb-3 mb-3">
                          {
                            CustomPost.gallery &&
                            CustomPost.gallery.map((prop, index) => (
                              <div key={index}>
                                <FluidImage image={prop} />
                              </div>
                            ))
                          }
                        </Slider>
                        <h3 className="detail-title">Thng tin chnh</h3>
                        <ul>
                          <li>Ch u t: <strong>SunShine Group</strong></li>
                          <li>a ch: <strong>{CustomPost.address}</strong></li>
                          <li>Loi hnh: <strong>Cn h chung c</strong></li>
                          <li className="price">Gi: t <strong>{CustomPost.price}</strong></li>
                        </ul>
                      </div>
                      </>
                    )
                  } 
              })()}
              
              <div className="info pb-3">
                
                {(() => {
                  if (CustomPost.address === null) {
                    return (
                      <>
                        <div>{contentParser({ content }, pluginOptions)}</div>
                        {/* <div dangerouslySetInnerHTML={{ __html: content }} /> */}
                      </>
                    )
                  } 
                  else{
                    return(
                      <>
                        <h3 className="detail-title">Gii thiu</h3>
                        {/* <div dangerouslySetInnerHTML={{ __html: content }} /> */}
                        <div>{contentParser({ content }, pluginOptions)}</div>
                      </>
                    )
                  }
                })()}
                
              </div>
              
              {(() => {
                if (CustomPost.location != null) {
                  return (
                    <div className="localtion pb-3 mb-2">
                      <h3 className="detail-title">V tr</h3>
                      <div dangerouslySetInnerHTML={{ __html: CustomPost.location }} />
                      
                    </div>
                  )
                } 
                
              })()}

              {(() => {
                if (CustomPost.utility != null) {
                  return (
                    <div className="utilities pb-3">
                      <h3 className="detail-title">Tin ch xung quanh</h3>
                      {
                              CustomPost.utility &&
                              CustomPost.utility.map((prop, index) => (
                                  <p key={index}>
                                      <i className="fa fa-hand-o-right" aria-hidden="true"></i> {prop.title}
                                  </p>
                              ))
                          }
                    </div>
                  )
                } 
                
              })()}

              
            </div>
          </div>
          <div className="col-md-3 col-xs-12">
            <aside>
              <div className="block">
                <h3>Sidebar</h3>
              </div>
            </aside>
          </div>
        </div>
      </div>
    </Layout>
  )
}

export default Post
